
.app-header {
  background-color: #1976d2;
  height: 60px;
  display: flex;
  justify-content: space-between;
  padding: 10px 20px;
  margin-bottom: 20px;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: #ffffff;
  font-weight: 700;
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
}



